import React, { Component } from 'react';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

const columns = ['Nombre', 'Cantidad', 'Acciones', 'Costo']

export default class ModalProductTable extends Component {

    render() {
        const { products } = this.props
        return (
            <Paper style={{ width: '100%' }}>
                <TableContainer style={{ maxHeight: '440px' }}>
                    <Table stickyHeader aria-label="sticky table">
                        <TableHead>
                            <TableRow>
                                {columns.map((column, key) => (
                                    <TableCell
                                        key={key}
                                    >
                                        {column}
                                    </TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                products && products.length === 0 &&
                                <TableRow tabIndex={-1} >
                                    <TableCell align="center" colSpan={3}>
                                        No se han agregado ingredientes.
                                        </TableCell>
                                </TableRow>
                            }
                            {
                                products && products.map((product, key) => {
                                    return (
                                        <TableRow key={key} hover role="checkbox" tabIndex={-1} >
                                            <TableCell>
                                                {product.name}
                                            </TableCell>
                                            <TableCell >
                                                {product.quantity}
                                            </TableCell>
                                            <TableCell >
                                                {product.actions}
                                            </TableCell>
                                            <TableCell >
                                                {product.cost}
                                            </TableCell>
                                        </TableRow>
                                    );
                                })
                            }
                            {
                                products && products.length > 0 &&
                                    <TableRow role="checkbox" tabIndex={-1} >
                                        <TableCell rowSpan={3}></TableCell>
                                        <TableCell colSpan={2} align='center'>
                                            Total
                                        </TableCell>
                                        <TableCell >
                                            {
                                                products.reduce((prev,next) => prev + parseInt(next.cost,10),0 )
                                            }
                                        </TableCell>
                                    </TableRow>
                            }
                        </TableBody>
                    </Table>
                </TableContainer>
            </Paper>
        );
    }
}